.flippy-container *
{
    box-sizing: border-box;
}

.flippy-cardContainer-wrapper
{
	position: relative;
	height: 100%;
    width: 100%;
}

.flippy-cardContainer
{
    height: 100%;
    width: 100%;
    position: relative;

    -webkit-transition: .6s;
            transition: .6s;
    -webkit-transform: perspective(1000px);
            transform: perspective(1000px);

    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.flippy-front,
.flippy-back
{
    position: relative;

    width: 100%;
    height: 100%;
    padding: 1em;

    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);

    background: transparent;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,.2);

    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.flippy-front
{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}

.flippy-back
{
    position: absolute;
    top: 0;
    left: 0;

    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
}

.flippy-cardContainer.isActive:not(.istouchdevice) .flippy-back,
.flippy-cardContainer.isActive.istouchdevice .flippy-back
{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}

.flippy-cardContainer.isActive:not(.istouchdevice) .flippy-front,
.fflippy-cardContainer.isActive.istouchdevice .flippy-front
{
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.flippy-cardContainer-wrapper.vertical .flippy-back
{
    -webkit-transform: rotateX(-180deg);
            transform: rotateX(-180deg);
}

.flippy-cardContainer-wrapper.vertical .flippy-cardContainer.isActive:not(.istouchdevice) .flippy-back,
.flippy-cardContainer-wrapper.vertical .flippy-cardContainer.isActive.istouchdevice .flippy-back
{
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
}

.flippy-cardContainer-wrapper.vertical .flippy-cardContainer.isActive:not(.istouchdevice) .flippy-front,
.flippy-cardContainer-wrapper.vertical .flippy-cardContainer.isActive.istouchdevice .flippy-front
{
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
            transform: rotateX(180deg);
}

.App {
  text-align: center;
  display: flex;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.flippy-container {
  margin-bottom: 20px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.jumbotron {
  position: relative;
  width: 100%;
  height: 500px;
  padding: 0;
  font-family: "Cormorant SC";
  box-shadow: 10px 5px 20px 2px lightslategrey;
}

.jumbotron .overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: auto;
  background: -webkit-gradient(linear, left top, left bottom, from(#333), to(black));
  background: -webkit-linear-gradient(#333, black);
  background: linear-gradient(#333, black);
  opacity: 0.5;
}

.jumbotron .background-image {
  height: 100%;
  background-image: url("https://www.valdosta.edu/student/diversity/images/sdi-students-in-a-row.jpg");
  background-size: cover;
}

.jumbotron .caption {
  position: absolute;
  top: 30%;
  width: 100%;
  text-align: center;
}

.jumbotron .caption h1, .jumbotron .caption p {
  color: white;
}

.jumbotron h1 {
  font-size: 80px;
  font-weight: 800;
}

.jumbotron p {
  font-size: 30px;
}

.quick-category-btn {
  position: center;
  font-size: 100px;
  border-radius: 50%;
  margin-top: 50%;
  height: 200px;
  width: 200px;
  padding: 17px;
  text-shadow: 4px 4px #9C081C;
  box-shadow: inset 0px 0px 60px 10px #222;
  border: solid #222 1px;
}

h2 {
  margin-top: 10px;
  color: white;
  text-shadow: 4px 4px #9C081C;
}
